import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                      C  U  E  N  T  A  S 
//............................................................

servicio.DesactivarCuenta = (cuenta) => {
    return base.post("/cuentas/desactivar", cuenta, {
        headers:{
            'Content-Type': 'application/json'
        }
    })
    .then(r => {
        return r.data
    })
    .catch(err => {
        console.log(err)
    })
}

servicio.NombreBancos = () => {
       return base.get(`/cuentas/nombresbancos`)
            .then(r => {
                // console.log(r.data.data);
                return r.data;
            })
            .catch(error => {
                return error;
            })
}

servicio.BancoXCuenta = (idProveedor) => {
    return base.get(`/cuentas/${idProveedor}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.Cuenta = (idCuenta) => {
    return base.get(`/cuentas/cuenta/${idCuenta}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.CuentaGuardar = (cuenta) => {
    return base.post("/cuentas/guardar", cuenta, {
        headers:{
            'Content-Type': 'application/json'
        }
    })
    .then(r => {
        return r.data
    })
    .catch(err => {
        console.log(err)
    })
}

export default servicio