import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                   P R O V E E D O R E S
//............................................................

servicio.proveedores = (codProveedor) => {
    if (codProveedor == null || codProveedor == undefined || codProveedor == 0) {
        return base.get(`/proveedores/todos`)
            .then(r => {
                return r.data;
            })
            .catch(error => {
                return error;
            })
    } else {
        console.log(codProveedor);
        return base.get(`/proveedores/${codProveedor}`)
            .then(r => {
                return r.data;
            })
            .catch(error => {
                return error;
            })
    }
}


servicio.guardarProveedor = (proveedor) => {
    return base.post("/proveedores/guardar", proveedor, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(e => {
            console.log(e);
        })
}

servicio.contactos = (idProveedor) => {
    return base.get(`/ContactosProv/${idProveedor}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.Contacto = (idContacto) => {
    return base.get(`/ContactosProv/contacto/${idContacto}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.ContactoGuardar = (contacto) => {
    return base.post("/ContactosProv/guardar", contacto, {
        headers: {
            'content-Type': 'application/json'
        }
    })
    .then(r => {
        return r.data
    })
    .catch(error => {
        return error;
    })
}

servicio.DesactivarProveedor = (proveedor) =>{
    return base.post("/proveedores/desactivar", proveedor, {
        headers:{
            'Content-Type': 'application/json'
        }
    })
    .then(r => {
        return r.data
    })
    .catch(err => {
        console.log(err)
    })
}

servicio.DesactivarContacto = (contacto) => {
    return base.post("/ContactosProv/desactivar", contacto, {
        headers:{
            'Content-Type': 'application/json'
        }
    })
    .then(r => {
        return r.data
    })
    .catch(err => {
        console.log(err)
    })
}
export default servicio