<template>
  <div class="container mt-3">
    <div class="mt-3">
      <div class="mt-3">
        <h3><i class="fas fa-credit-card"></i> Cuentas de {{this.NombreProveedor}}</h3>
      </div>
     

      <div>
            <b-row class="my-2">
                <b-col md="4">  
                  <b-form-group label="Filtrar"
                              label-cols-sm="1"
                              label-cols-md="2"
                              label-size="sm"
                              label-for="filterInput">
                      <b-input-group size="sm">
                          <b-form-input v-model="filter"
                                        type="search"
                                        id="filterInput"
                                        placeholder="Escribe el filtro..."
                                        autocomplete="off">
                          </b-form-input>
                          <b-input-group-append>
                              <b-button :disabled="!filter" @click="filter = ''">
                                  <i class="fas fa-times"></i>
                              </b-button>
                          </b-input-group-append>
                      </b-input-group>
                  </b-form-group>                   
                </b-col>

                <b-col md="3">
                  <b-form-group label="Por página"
                                label-cols-sm="2"
                                label-cols-md="2"
                                label-cols-lg="4"
                                label-size="sm"
                                label-for="perPageSelect">
                    <b-form-select v-model="perPage"
                                    id="perPageSelect"
                                    size="sm"
                                    :options="pageOptions">
                    </b-form-select>
                  </b-form-group>
                </b-col>

                 <b-col md="3" offset-md="2">
                            <b-button
                                @click="limpiarDatos()"
                                v-b-modal.modalRegistroCuenta
                                variant="primary"
                                class="btn btn-sm py-1 mt-1 btn-block"
                                >
                                <i class="fa fa-plus"></i> Nueva
                            </b-button>
                 </b-col>
            </b-row>
            <!-- Main table element -->
            <b-table small
                    striped 
                    bordered
                    stacked="md"
                    head-variant="dark"
                    :items="items"
                    :fields="fields"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :filterIncludedFields="filterOn"
                    @filtered="onFiltered"
                    >

                <template v-slot:cell(estatus)="row">
                  <div style="width: 5px">
                      <img id="imagen" src="https://img.icons8.com/color/48/000000/verified-badge.png" v-if=row.item.cBPrincipal />
                  </div>
                </template>

                <template v-slot:cell(acciones)="row">
                    <b-button 
                    size="sm"
                    class="mr-1"
                    v-b-tooltip.hover
                    variant="outline-info"
                    title="Ver detalle"
                    @click="cargarDatosCuenta(row.item.idCuenta)"
                    v-b-modal.modalInfoCuenta>
                      <i class="fa fa-eye"/>
                    </b-button>
                    
                    <b-button
                    size="sm"
                    class="mr-1"
                    v-b-tooltip.hover
                    variant="outline-info"
                    title="Editar"
                    @click="cargarDatosCuenta(row.item.idCuenta)"
                    v-b-modal.modalRegistroCuenta>
                      <i class="fa fa-edit"/>
                    </b-button>

                    <b-button 
                    size="sm"
                    class="mr-1"
                    variant="danger"
                    title="Desactivar"
                    @click="desactivar(row.item.idCuenta)"
                    >
                      <i class="fa fa-times"/>
                    </b-button>
                    
                </template>
            </b-table>

            <b-pagination v-model="currentPage"
                          class="mt-1"
                          :total-rows="totalRows"
                          :per-page="perPage"
                          align="center"
                          aria-controls="my-table">
            </b-pagination>


            <!-- Modal detalles  de cuenta -->
        <b-modal
        id="modalInfoCuenta"
        title="Detalles de cuenta"
        hide-footer
        >
            <b-card
            border-variant="light">
               <b-container>
                    
                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" md=5> Nombre: </b-col>
                        <b-col md=7>{{this.form.Nombre}}</b-col>  
                    </b-row>

                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" sm=5> Número de Cuenta: </b-col>
                        <b-col sm=7> {{this.form.NumeroCuenta}} </b-col> 
                    </b-row>

                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" sm=5> CLABE: </b-col>
                        <b-col sm=7> {{this.form.CLABE}} </b-col>  
                    </b-row>
                 
                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" sm=5>  Principal: </b-col>
                        <b-col sm=7>
                            <i class="fas fa-check-square fa-2x text-primary" v-if="form.Principal"/>
                            <i class="fas fa-window-close fa-2x text-danger" v-else/>
                        </b-col>   
                    </b-row>
                   
                </b-container>
            </b-card>       
        </b-modal>

            <!-- Modal guardar cuenta -->

        <b-modal
        id="modalRegistroCuenta"
        title="Registro cuenta"
        centered
        @ok="handleOk"
        >
            <form ref="form" @submit.stop.prevent="guardarCuenta">

               <label ><i class="fas fa-landmark"/> Banco </label>
                <b-form-select id="cboBanco" 
                v-model="form.Nombre" 
                :options="bancos" 
                />

                <label class="mt-3" ><i class="far fa-credit-card"/> Número de cuenta </label>
                <b-form-input
                    id="NumeroCuenta"
                    v-model="form.NumeroCuenta"  
                    autocomplete="off"
                />

                <label class="mt-3" ><i class="fas fa-credit-card"/> CLABE </label>
                <b-form-input
                    id="CLABE"
                    v-model="form.CLABE"  
                    autocomplete="off"
                />
            
                <b-form-checkbox class="mt-3" v-model="form.Principal" >
                Cuenta Principal
                </b-form-checkbox>
            </form>    
        </b-modal> 

        </div>
    </div>
  </div>
</template>

<script>
import servicioCuentas from "@/services/servicio-cuentas";
import servicioProveedores from "@/services/servicio-proveedores"
export default {
  data() {
    return {
      rutaNuevo: "proveedores/registro/",
      NombreProveedor: "",
      bancos: [{ value:null, disabled: true, text: "seleccione un banco"}],
      items: [],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      filter: null,
      filterOn: [],
      form:{
          IdCuenta: 0,
          Nombre : "",
          NumeroCuenta: "",
          CLABE: "",
          Principal: false             
      },
      fields: [
        {
          key: "estatus",
          label: "  "
        },
        {
          key: "nombre",
          label: "Banco",
          sortable: true,
          sortDirection: "desc",
        },
        {
          key: "numeroCuenta",
          label: "NumeroCuenta",
          sortable: true,
          class: "text-center",
        },
        {
          key: "acciones",
          label: "Acciones",
          sortable: false,
          class: "text-center"
        }
      ],
      permisoEscritura: true,
    };
  },
  components: {

  },
  async mounted() {
    var id = this.$route.params.id
    await this.consultarCuentas(id);
    await this.consultarProveedor(id);
    await this.CargarRenglones();
    this.cargaBancosNombres();
  },
  computed:{
    sortOptions() {
      // Create an options list from our fields
      return this.fields
                  .filter((f) => f.sortable)
                  .map((f) => {
                      return { text: f.label, value: f.key };
                  });
      },
  },

  methods: {
    CargarRenglones() {
        setTimeout(() => {
            this.totalRows = this.items.length;
            console.log(this.totalRows)
        }, 1000);
    },
    onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
    },
      desactivar(id){
      let cuenta = {
        idCuenta : id,
        activado : 0
      }
      this.$swal.fire({
        title: "¿Desdea eliminar la cuenta?",
        text: "se perderan los datos de la cuenta",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Si",
        cancelButtonText: "No",
      }).then((result) => {
        if (result.value){
          servicioCuentas.DesactivarCuenta(cuenta).then((r) => {
              this.$swal.fire({
                  text: r.message,
                  icon: "success"
              });
              let this_=this;
              setTimeout(function(){this_.$router.go()}, 1000)
          });  
        }
      })
    },

    redirect() {
            setTimeout(() => {
                this.$router.push({ path: `${this.rutaNuevo}0` });
            }, 500);
        },
   
    async consultarProveedor(id){
        this.$loading(true);
        await servicioProveedores.proveedores(id)
        .then((r) => {
            console.log(r.data.proveedor[0].nombreComercial)
            this.NombreProveedor = r.data.proveedor[0].nombreComercial;
            this.$loading(false);
        });
    },

    handleOk(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.guardarCuenta()
    },

    guardarCuenta(){
        let cuenta = {
            idCuenta: this.form.IdCuenta,
            idProveedor: this.$route.params.id,
            idBanco: this.form.Nombre,
            numeroCuenta: this.form.NumeroCuenta,
            clabe: this.form.CLABE,
            cBPrincipal: this.form.Principal
        } 
        console.log(cuenta)
        servicioCuentas.CuentaGuardar(cuenta).then((r) => {
            console.log(r);
            if(r.value){
                this.$swal.fire({
                    text: r.message,
                    icon: "success"
                });
                let this_=this;
                setTimeout(function(){this_.$router.go()}, 1000)            
            }else {
                this.$swal.fire({
                    text: r.message,
                    icon: "warning"
                });
            }
        });
    },

    limpiarDatos(){
        this.form.IdCuenta = 0
        this.form.Nombre = ""
        this.form.NumeroCuenta = ""
        this.form.CLABE = ""
        this.form.Principal = false
    },

    async cargarDatosCuenta(id){
      this.$loading(true);
      await servicioCuentas.Cuenta(id)
        .then((r) => {
        this.CargarRenglones();
          console.log(r)
          this.form.IdCuenta = r.data.cuenta[0].idCuenta
          this.form.Nombre = r.data.cuenta[0].idBanco
          this.form.NumeroCuenta = r.data.cuenta[0].numeroCuenta
          this.form.CLABE = r.data.cuenta[0].cLABE
          this.form.Principal = r.data.cuenta[0].cBPrincipal
          this.$loading(false);
        })
    },

    async consultarCuentas(id) {
      this.$loading(true);
     await servicioCuentas.BancoXCuenta(id)
        .then((r) => {
          console.log(r)
          this.items = r.data.cuenta;
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
          this.$loading(false);
        });
    },
    cargaBancosNombres(){
      this.$loading(true);
        servicioCuentas.NombreBancos().then((r) => {
          r.data.bancos.forEach((x) => {
            this.bancos.push({ value: x.idBanco, text: x.nombre});
            this.$loading(false);
         })
        })
    },
  },
};
</script>
<style scoped>
</style>
<style>
.hidden_header {
  display: none;
}
#imagen {
    display: block;
    margin: 0 auto;
    width:30px;
}
</style>